a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}
a.social:hover {
  transform: translateY(-2px);
}
a.youtube {
  color: #eb3223;
}
a.linkedin {
  color: #0077b5;
}
a.twitter {
  color: #49a1eb;
}
#container {
  height: 100%;
  width: 100%;
  display: flex;
}
#left {
  width: 50%;
}
#right {
  width: 50%;
  text-align: right;
}
